import { useEffect } from "react";
import raw from "../hooks/functions/divvy_dataset.json";
import PieChart from "./PieChart";
import { Checkbox, Grid } from "theme-ui";
import { useState } from "react";
const SelectMode = ({
    data,
    changeData,
    renderMode,
    changeRenderMode,
    chosenData,
    changeChosenData,
    ...props
}) => {
    useEffect(() => {
        changeData(raw);
        changeRenderMode("selectmode");
    }, []);

    const [checkBoxes, changeCheckBoxes] = useState([true, true, true, true]);
    return (
        <div>
            <h1>Hello World</h1>
            <PieChart data={data} chosenData={chosenData} />
            <Grid gap={2} columns={4}>
                <Checkbox
                    checked={checkBoxes[0]}
                    onClick={() =>
                        changeCheckBoxes([
                            checkBoxes[0] ? false : true,
                            checkBoxes[1],
                            checkBoxes[2],
                            checkBoxes[3],
                        ])
                    }
                >
                    Member Electric
                </Checkbox>
                <Checkbox
                    checked={checkBoxes[1]}
                    onClick={() =>
                        changeCheckBoxes([
                            checkBoxes[0],
                            checkBoxes[1] ? false : true,
                            checkBoxes[2],
                            checkBoxes[3],
                        ])
                    }
                >
                    Member Normal
                </Checkbox>
                <Checkbox
                    checked={checkBoxes[2]}
                    onClick={() =>
                        changeCheckBoxes([
                            checkBoxes[0],
                            checkBoxes[1],
                            checkBoxes[2] ? false : true,
                            checkBoxes[3],
                        ])
                    }
                >
                    Casual Electric
                </Checkbox>
                <Checkbox
                    checked={checkBoxes[3]}
                    onClick={() =>
                        changeCheckBoxes([
                            checkBoxes[0],
                            checkBoxes[1],
                            checkBoxes[2],
                            checkBoxes[3] ? false : true,
                        ])
                    }
                >
                    Casual Normal
                </Checkbox>
            </Grid>
        </div>
    );
};

export default SelectMode;
